    </div>
    
    <div
        class="echart"
        id="mychart3"
        :style="{ float: 'left', width: '100%', height: '400px' }"
      ></div>
    
  </div>
</template>

<script>
import * as echarts from "echarts";
import 'echarts-wordcloud';
import http from "../../shared/Http.js";

export default {
  name: "Welcome",
  data() {
    return {
      port_num: '',
      ship_num: '',
      sum_shouru: '',
      avg_comment: '',
      ord_ok_num:'',
      ord_not_ok_num:'',
      cargo_ok_num:'',
      user_num:'',
      option1: {},
      option2 : {
     
      },
      option3 : {
       
        },
    } 
  },
  async mounted(){
    const today = new Date();

    // 定义一个数组，用于保存过去7天的日期
    const last7Days = [];

    // 循环7次，分别获取过去7天的日期
    for (let i = 0; i < 7; i++) {
      // 使用 setDate() 方法计算过去的日期
      const day = new Date(today);
      day.setDate(today.getDate() - i);
      
      // 将日期按照指定格式保存到数组中
      const formattedDate = `${day.getFullYear()}-${day.getMonth() + 1}-${day.getDate()}`;
      last7Days.push(formattedDate);
    }
    const response = await http
            .get("/getall",
            )
            .catch((error) => console.log(error));
    const data = response.data
    this.port_num = data.port_num
    this. ship_num = data.ship_num
    this.sum_shouru = data.sum_shouru
    this.ord_not_ok_num = data.ord_not_ok_num
    this.ord_ok_num = data.ord_ok_num
    this.cargo_ok_num = data.cargo_ok_num
    this.user_num = data.user_num
    this.avg_comment = data.avg_comment
    this.option2 = {
                title: {
                  text: "货物分类",
                  left: "center",
                  top: "center",
                },
                series: [
                  {
                    type: "pie",
                    data: response.data["seriesData"],
                    radius: ["40%", "70%"],
                  },
                ],
             }
    this.option1 = {
                series: [{
                  
                    type: 'wordCloud',
                    shape: 'circle',
                    keepAspect: false,
                   //maskImage: maskImage,
                    sizeRange: [15, 80],
                    rotationRange: [0, 0],
                    rotationStep: 45,
                    gridSize: 8,
                    shape: 'pentagon',
                    width: '100%',
                    height: '100%',
                    textStyle: {
            fontFamily: 'sans-serif',
            fontWeight: 'bold',
            // Color can be a callback function or a color string
            color: function () {
                // Random color
                return 'rgb(' + [
                    Math.round(Math.random() * 160),
                    Math.round(Math.random() * 160),
                    Math.round(Math.random() * 160)
                ].join(',') + ')';
            }
        },
                    data: data.ciyunData
                }]
            }
         
        
          this.option3 = {
            title: {
						text: '近一星期订单数量折线图',
						/*textVerticalAlign:'center'*/
						left:'center'
					},
          xAxis: {
            type: 'category',
            data: last7Days.reverse()
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              data: data.counts,
              type: 'line'
            }
          ]
          }
    this.initEcharts()
  },
  methods: {
    initEcharts() {
      
      const myChart1 = echarts.init(document.getElementById("mychart1"));// 图标初始化
      myChart1.setOption(this.option1);// 渲染页面

      const myChart2 = echarts.init(document.getElementById("mychart2"));// 图标初始化
      myChart2.setOption(this.option2);// 渲染页面

      const myChart3 = echarts.init(document.getElementById("mychart3"));// 图标初始化
      myChart3.setOption(this.option3);// 渲染页面
      //随着屏幕大小调节图表
      window.addEventListener("resize", () => {
        myChart1.resize();
        myChart2.resize();
        myChart3.resize();

      });
    }
  },
};
</script>

