<template>
  <div class="main">
    <p></p>
    <el-form
      :model="editDataForm"
      status-icon
      label-width="100px"
      class="demo-ruleForm"
    >
    <el-form-item label="船名" >
                <el-input  v-model="shipForm.ship_name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="船型" >
                <el-select v-model="typeValue" placeholder="请选择">
                    <el-option
                        v-for="item in typeOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
                </el-form-item>
                <el-form-item label="容量" >
                    <el-input  v-model="shipForm.capacity" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="现处港口" >
                    <el-select v-model="portValue" placeholder="请选择">
                    <el-option
                        v-for="item in portOptions"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                    </el-option>
                </el-select>
                </el-form-item>
                <el-form-item label="速度 km/h" >
                    <el-input  v-model="shipForm.speed" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="每公里价格（元）" >
                    <el-input  v-model="shipForm.price" autocomplete="off"></el-input>
                </el-form-item>
               
               
              
      <el-form-item>
        <el-button type="primary" @click="submitForm()">提交</el-button>
        <el-button @click="resetForm()">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import http from "../../shared/Http.js";

export default {
  name: "ShipEdit",

  data() {
    return {
      typeOptions: [
        {
          value: "1",
          label: "重型",
        },
        {
          value: "2",
          label: "中型",
        },
        {
          value: "3",
          label: "小型",
        },
      ],
      portOptions: [],
      typeValue: "",
      portValue: "",
      id: "",
      shipForm: {
        ship_name: "",
        type_id: "",
        capacity: "",
        port_id: "",
        price: "",
        speed: "",
      },
    };
  },
  async beforeMount() {
    this.id = this.$route.query.id;
    const portResponse = await http
            .get("/ports")
            .catch((error) => console.log(error));
            this.portOptions = portResponse.data;
  },
  methods: {
    async submitForm() {
        this.shipForm.type_id =  this.typeValue
            this.shipForm.port_id =  this.portValue
      const response = await http
        .post(
          "/ships/edit",
          {
            params: { id: this.id, form: this.shipForm },
          },
          {
            headers: {
              //头部参数
              "Content-Type": "application/json",
            },
          }
        )
        .catch((error) => console.log(error));
      if (response.status === 200) {
        this.$message({
          message: "恭喜你，这是一条成功消息",
          type: "success",
        });
      } else {
        this.$message.error("错了哦，这是一条错误消息");
      }
    },
    resetForm() {
      Object.assign(this.editDataForm, {
        name: "",
        password: "",
        phone: "",
      });
    },
  },
};
</script>

