<template>
    <div class="main">
        <p></p>
        <el-form :model="editDataForm" status-icon   label-width="100px" class="demo-ruleForm">
        <el-form-item label="账号" prop="name">
            <el-input type="text" v-model="editDataForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="editDataForm.password" autocomplete="off"></el-input>
        </el-form-item>
       
        <el-form-item label="手机" prop="phone">
            <el-input v-model.number="editDataForm.phone"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="submitForm()">提交</el-button>
            <el-button @click="resetForm()">重置</el-button>
        </el-form-item>
        </el-form>
    </div>
</template>

<script>
import http from "../../shared/Http.js";

export default {
    name:"UserEdit",
    
    data(){
        return{
            id:'',
            editDataForm:{
                name:'',
                password:'',
                phone:'',

            }
        }
    },
    beforeMount(){
         this.id = this.$route.query.id
    },
    methods:{
        async submitForm(){
            const response = await http.post('/users/edit',{
              params: {"id":this.id,form:this.editDataForm}},{        
                headers: {  //头部参数
                'Content-Type': 'application/json',
                }
            }
          ).catch(error =>console.log(error))
          if(response.status === 200){
            this.$message({
              message: '恭喜你，这是一条成功消息',
              type: 'success'
            });
           
          }else{
            this.$message.error('错了哦，这是一条错误消息');
          }
        },
        resetForm(){
            Object.assign(this.editDataForm,{
                name:'',
                password:'',
                phone:'',

            })
        }
    }

}
</script>

