<template>
    <div>
        <el-card
      class="filter-container"
      shadow="never"
      style="margin-bottom=30px;"
    >
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button
          style="float: right"
          type="primary"
          @click="searchCargo"
          size="small"
        >
          查询搜索
        </el-button>
        <el-button style="float: right; margin-right: 15px" size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" size="small" label-width="140px">
          <el-form-item label="输入搜索：">
            <el-input
              class="input-width"
              placeholder="货物名"
              clearable
              v-model="search_input"
            ></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card
      class="operate-container"
      shadow="never"
      style="margin-bottom=30px;"
    >
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
      <el-button
        size="mini"
        class="btn-add"
        @click="dialogFormVisible = !dialogFormVisible"
        style="margin-left: 20px"
        >添加</el-button
      >
      <el-button
        type="primary"
        icon="el-icon-refresh"
        circle
        @click="refresh"
      ></el-button>
    </el-card>
          <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
            prop="id"
            label="货物号"
            align="center"

            width="100">
            </el-table-column>
            <el-table-column
            prop="name"
            label="货名"
            align="center"

            >

            </el-table-column>
            <el-table-column
                prop="type"
                label="货物类型"
               
                align="center"

            >
            
            </el-table-column>
            <el-table-column
            prop="weight"
            label="重量 kg"
            
            align="center"

            >
            
            </el-table-column>
            <el-table-column
            prop="user"
            label="所属用户"
            
            align="center"

            >
            
            </el-table-column>
            <el-table-column
            prop="status"
            label="是否运输完成"
            
            align="center"

            >
            <template slot-scope="scope">
                 <div v-if="scope.row.status===false">
                
                <el-button type="warning">未完成</el-button>
            </div>
            <div v-else>
                <el-button type="success">已完成</el-button>
            </div>
            </template>
            <!-- <p>{{ status }}</p> -->
           
            </el-table-column>
            <el-table-column label="操作" align="center" width="180">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog title="添加货物" :visible.sync="dialogFormVisible" width="30%"  >
            <el-form >
            <el-form-item label="货名" >
                    <el-input  v-model="cargoForm.cargo_name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="货型" >
                    <el-input  v-model="cargoForm.cargo_type" autocomplete="off"></el-input>
                </el-form-item>
              
                <el-form-item label="重量 kg" >
                    <el-input  v-model="cargoForm.weight" autocomplete="off"></el-input>
                </el-form-item>
                
                <el-form-item label="所属用户Id" >
                    <el-input  v-model="cargoForm.user_id" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item style="">
                    <el-button type="primary" @click="addCargo" style="margin-left: 10vw;margin-top: 10px;">提交</el-button>
                    <el-button @click="resetForm('cargoForm')" >重置</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
import http from "../../shared/Http.js";

export default {
    name: "CargoInfoManager",
    data() {
      return {
        dialogFormVisible:false,
        search_input: '',
        radio: '1',
        cargoForm:{
            cargo_name:'',
            cargo_type:'',
            weight:'',
            user_id:''
        },
        tableData: []
      }
    },
    async beforeMount() {
    const response = await http
      .get("/cargos")
      .catch((error) => console.log(error));

    this.tableData = response.data;
    console.log(this.tableData)
    },
    methods: {
        refresh() {
      window.location.reload();
    },
        resetForm(formName) {
         Object.assign(this.cargoForm,{
            cargo_name:'',
            cargo_type:'',
            
            weight:''
        })
      },
      async addCargo() {
      const response = await http
        .post("/cargos/add",
          {
            params: this.cargoForm,
          },
          {
            headers: {
              //头部参数
              "Content-Type": "application/json",
            },
          }
        )
        .catch((error) => console.log(error));
      if (response.status === 200) {
        this.$message({
          message: "恭喜你，这是一条成功消息",
          type: "success",
        });
      } else {
        this.$message.error("错了哦，这是一条错误消息");
      }
      this.dialogFormVisible = false;
    },
    async searchCargo() {
      const response = await http
        .post(
          "/cargos/search",
          {
            params: { name: this.search_input },
          },
          {
            headers: {
              //头部参数
              "Content-Type": "application/json",
            },
          }
        )
        .catch((error) => console.log(error));
      if (response.status === 200) {
        this.$message({
          message: "恭喜你，这是一条成功消息",
          type: "success",
        });
      } else {
        this.$message.error("错了哦，这是一条错误消息");
      }
      this.tableData = response.data;
    },
    async handleDelete(index, row) {
      const response = await http
        .post(
          "/cargos/delete",
          {
            params: { id: row["id"] },
          },
          {
            headers: {
              //头部参数
              "Content-Type": "application/json",
            },
          }
        )
        .catch((error) => console.log(error));
      if (response.status === 200) {
        this.$message({
          message: "恭喜你，这是一条成功消息",
          type: "success",
        });
      } else {
        this.$message.error("错了哦，这是一条错误消息");
      }
    },
    handleEdit(index, row) {
      this.$router.push({
        name: `cargoedit`, // 只是把query改了，其他都没变
        query: {
          id: row["id"],
        },
      });
    },
    }
}
</script>

