
import Vue from 'vue'
import Router from 'vue-router'

import Dashboard from '../views/Dashboard.vue'

import Welcome from '../components/dashboard/welcome.vue'
import ShipEdit from '../components/ship/shipEdit.vue'

import ShipInfoManager from '../components/ship/shipInfoMananger.vue'
import CargoInfoManager from '../components/cargo/CargoInfoManager.vue'
import CargoEdit from '../components/cargo/CargoEdit.vue'

import OrderManager from '../components/order/orderManager.vue'
import UserManager from '../components/account/userManager.vue'
import UserEdit from '../components/account/userEdit.vue'


// import Welcome from '../components/dashboard/leftMenu/adminMe'

import ShipRoute from '../components/map/useShipRoute.vue'
import PortManager from '../components/map/portManage.vue'
import Tasks from '../components/task/task.vue'
import OrderDesc from '../components/task/description.vue'
import Login from '../views/Login.vue'




Vue.use(Router)
const routes = [
    {
        name:'root',
        path: '/',
        component:Login
    },
   //admin
    {
        name:'dashboard',
        path: '/dashboard',
        component:Dashboard  ,
        children:[
          {
            path:'',
            component:Welcome
          }
        ]
    },
    {
      name:'account',
      path: '/account',
      component:Dashboard  ,
      children:[
        {
          path: 'users',
          component: UserManager,
         
        },
        {
          path: 'edit',
          name:'useredit',
          component: UserEdit,
         
        },
        
      ]
    },
    {
      name:'ship',
      path: '/ship',
      component:Dashboard  ,
      children:[
        {
          path: 'info',
          component: ShipInfoManager
        },
        {
          path: 'edit',
          name:'shipedit',
          component: ShipEdit,
         
        },
      ]
    },
    {
      name:'cargo',
      path: '/cargo',
      component:Dashboard  ,
      children:[
        {
          path: 'info',
          component: CargoInfoManager
        },
        {
          path: 'edit',
          name:'cargoedit',
          component: CargoEdit,
         
        },

      ]
    },
    {
      name:'task',
      path: '/task',
      component:Dashboard  ,
      children:[
        {
          path: '',
          component: Tasks
        },
        {
          name:"orderDesc",
          path: '/description',
          component: OrderDesc
        },
      ]
    },
    {
      name:'route',
      path: '/shiproute',
      component:Dashboard  ,
      children:[
        {
          path: '',
          component: ShipRoute
        },
        {
          path: 'port',
          component: PortManager
        },
        
      ]
    },
    {
      name:'order',
      path: '/order',
      component:Dashboard  ,
      children:[
        {
          path: '',
          component: OrderManager
        }
      ]
    },
    //user
    
    // {
    //     name:'user_info',
    //     path: '/userinfo/:name', // :id 相当于 router-link 中的 params 的id
    //     components:{
    //         main:UserInfo,
    //     }

    // }
  ]

const router = new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router