<template>
  <div class="login body" data-admin-utc-offset="28800">
    <!-- Container -->
    <div id="container">
      <!-- Header -->
      <div id="header">
        <div id="branding">
          <h1 id="site-name">船舶物流运输系统管理</h1>
        </div>
      </div>
      <!-- END Header -->

      <div class="main shifted" id="main">
        <div class="content">
          <!-- Content -->
          <div id="content" class="colM">
            <div id="content-main">
              <form
               
                method="post"
                id="login-form"
              >
               
                <div class="form-row">
                  <label class="required" for="id_username">用户名:</label>
                  <input
                    v-model="name"
                    type="text"
                    name="username"
                    autofocus=""
                    autocapitalize="none"
                    autocomplete="username"
                    maxlength="150"
                    required=""
                    id="id_username"
                  />
                </div>
                <div class="form-row">
                  <label class="required" for="id_password">密码:</label>
                  <input
                    v-model="password"
                    type="password"
                    name="password"
                    autocomplete="current-password"
                    required=""
                    id="id_password"
                  />
                  <input type="hidden" name="next" value="/admin/" />
                </div>

                <div class="submit-row">
                  <input type="button" value="登录" @click="submitLogin"/>
                </div>
              </form>
            </div>

            <br class="clear" />
          </div>
          <!-- END Content -->
          <div id="footer"></div>
        </div>
      </div>
    </div>
    <!-- END Container -->

   
  </div>
</template>

<script>
import http from "../shared/Http.js";

export default {
  name: "Login",
  data() {
    return {
      name: "",
      password: "",
    };
  },
  methods: {
    async submitLogin() {
      const response = await http
        .post(
          "/adminlogin",
          {
            params: { name: this.name, password: this.password },
          },
          {
            headers: {
              //头部参数
              "Content-Type": "application/json",
            },
          }
        )
        .catch((error) => {
          console.log(error);
          this.$message.error("登录失败");
        });

      if (response.status === 200) {
        this.$message({
          message: "登录成功",
          type: "success",
        });
        this.$router.push({
          name: `dashboard`, // 只是把query改了，其他都没变
        });
        window.location.reload();
       
      }
     
    },
  },
};
</script>

