<template>
    <div>
        <el-card
      class="filter-container"
      shadow="never"
      style="margin-bottom=30px;"
    >
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button
          style="float: right"
          type="primary"
          @click="searchOrderByUser"
          size="small"
        >
          查询搜索
        </el-button>
        <el-button style="float: right; margin-right: 15px" size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" size="small" label-width="140px">
          <el-form-item label="输入搜索：">
            <el-input
              class="input-width"
              placeholder="帐号"
              clearable
              v-model="search_input"
            ></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card
      class="operate-container"
      shadow="never"
      style="margin-bottom=30px;"
    >
      <i class="el-icon-tickets"></i>
      <span style="margin-right: 20px;">数据列表</span>
     
      
      <el-button
        type="primary"
        icon="el-icon-refresh"
        circle
        @click="refresh"
      ></el-button>
    </el-card>
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
            prop="id"
            label="订单号"
            width="70">
            </el-table-column>
            <el-table-column
            prop="orderNum"
            label="订单编号"
            width="70">
            </el-table-column>
            <el-table-column
            prop="cargo"
            label="货名"
            width="100">
            </el-table-column>
            <el-table-column
            prop="ship"
            label="船舶"
            width="100"
            >
            
            </el-table-column>
            <el-table-column
            prop="start_port"
            label="起始港口"
            
            >
            </el-table-column>
            
            <el-table-column
            prop="end_port"
            label="目的港口"
            
            ></el-table-column>
            <el-table-column
            prop="price"
            label="价格"
            
            ></el-table-column>
            
            <el-table-column  
           
            prop="comment"
            label="评价"
    
            > <template slot-scope="scope">
              <div v-if="scope.row.is_commented===true" > 
                  {{ scope.row.comment }}
                  
              </div> 
              <div v-else>未评价</div>
            </template>
          </el-table-column>
           
            <el-table-column
            prop="status"
            label="订单状态"
            width="100"
            >
            
            </el-table-column>
            <el-table-column
            prop="user"
            label="客户名"
           
            >
            
            </el-table-column>
            <el-table-column label="操作" width="180">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">详情</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
       
    </div>
</template>

<script>
import http from "../../shared/Http.js";

export default {
    name: "OrderManager",
    data() {
      return {
        dialogFormVisible:false,
        search_input: '',
        radio: '1',
        cargoForm:{
            cargo_name:'',
            cargo_type:'',
            capacity:'',
            weight:'',
            
        },
        tableData: []
      }
    },
    async beforeMount(){
        const response = await http
            .get("/orders")
            .catch((error) => console.log(error));
      
            this.tableData = response.data

    },
    methods: {
        refresh(){
             window.location.reload();
        },
        async searchOrderByUser() {
      const response = await http
        .post(
          "/orders/search",
          {
            params: { name: this.search_input },
          },
          {
            headers: {
              //头部参数
              "Content-Type": "application/json",
            },
          }
        )
        .catch((error) => console.log(error));
      if (response.status === 200) {
        this.$message({
          message: "恭喜你，这是一条成功消息",
          type: "success",
        });
      } else {
        this.$message.error("错了哦，这是一条错误消息");
      }
      this.tableData = response.data;
        }, 
        async handleDelete(index, row) {
            const response = await http
            .post(
            "/orders/delete",
            {
                params: { id: row["id"] },
            },
            {
                headers: {
                //头部参数
                "Content-Type": "application/json",
                },
            }
                )
                .catch((error) => console.log(error));
            if (response.status === 200) {
                this.$message({
                message: "恭喜你，这是一条成功消息",
                type: "success",
                });
            } else {
                this.$message.error("错了哦，这是一条错误消息");
            }
        },
        handleEdit(index,row){
          this.$router.push({
          name: `orderDesc`, // 只是把query改了，其他都没变
          query: {
            id: row["orderNum"],
          },
      });
        },
        
    }
}
</script>

