<template>
  <div>
    <el-card
      class="filter-container"
      shadow="never"
      style="margin-bottom=30px;"
    >
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button
          style="float: right"
          type="primary"
          @click="searchUser"
          size="small"
        >
          查询搜索
        </el-button>
        <el-button style="float: right; margin-right: 15px" size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" size="small" label-width="140px">
          <el-form-item label="输入搜索：">
            <el-input
              class="input-width"
              placeholder="帐号"
              clearable
              v-model="search_input"
            ></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card
      class="operate-container"
      shadow="never"
      style="margin-bottom=30px;"
    >
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
      <el-button
        size="mini"
        class="btn-add"
        @click="dialogFormVisible = !dialogFormVisible"
        style="margin-left: 20px"
        >添加</el-button
      >
      <el-button
        type="primary"
        icon="el-icon-refresh"
        circle
        @click="refresh"
      ></el-button>
    </el-card>
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column prop="id" label="编号" width="100" align="center">
      </el-table-column>
      <el-table-column prop="name" label="账号" align="center">
      </el-table-column>
      <el-table-column prop="password" label="密码" align="center">
      </el-table-column>
      <el-table-column prop="phone" label="手机" align="center">
      </el-table-column>
      <el-table-column
        prop="create_time"
        label="添加时间"
        width="160"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="last_login"
        label="最后登录"
        width="160"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="status"
        label="是否启用"
        width="140"
        align="center"
      >
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            :active-value="true"
            :inactive-value="false"
            @click.native="handleSwitch(scope.$index + 1, scope.row)"
          >
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" width="180">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index + 1, scope.row)"
            >编辑</el-button
          >
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index + 1, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="添加账号" :visible.sync="dialogFormVisible" width="30%">
      账号：<el-input
        v-model="userForm.name"
        placeholder="请输入账号"
      ></el-input>
      密码：<el-input
        v-model="userForm.password"
        placeholder="请输入密码"
      ></el-input>
      手机：<el-input
        v-model="userForm.phone"
        placeholder="请输入手机号"
      ></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false" size="small"
          >取 消</el-button
        >
        <el-button
          type="primary"
          @click="handleAllocDialogConfirm()"
          size="small"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import http from "../../shared/Http.js";

export default {
  name: "UserManager",
  data() {
    return {
      dialogFormVisible: false,
      search_input: "",
      radio: "1",
      userForm: {
        name: "",
        password: "",
        phone: "",
      },
      tableData: [],
    };
  },

  async beforeMount() {
    const response = await http
      .get("/users")
      .catch((error) => console.log(error));

    this.tableData = response.data;
  },
  methods: {
    refresh() {
      window.location.reload();
    },
    resetForm(formName) {
      search_input = "";
    },
    async handleAllocDialogConfirm() {
      const response = await http
        .post(
          "/users/add",
          {
            params: this.userForm,
          },
          {
            headers: {
              //头部参数
              "Content-Type": "application/json",
            },
          }
        )
        .catch((error) => console.log(error));
      if (response.status === 200) {
        this.$message({
          message: "恭喜你，这是一条成功消息",
          type: "success",
        });
      } else {
        this.$message.error("错了哦，这是一条错误消息");
      }
      this.dialogFormVisible = false;
    },
    async handleDelete(index, row) {
      const response = await http
        .post(
          "/users/delete",
          {
            params: { id: row["id"] },
          },
          {
            headers: {
              //头部参数
              "Content-Type": "application/json",
            },
          }
        )
        .catch((error) => console.log(error));
      if (response.status === 200) {
        this.$message({
          message: "恭喜你，这是一条成功消息",
          type: "success",
        });
      } else {
        this.$message.error("错了哦，这是一条错误消息");
      }
    },
    async handleSwitch(index, row) {
      const response = await http
        .post(
          "/users/switch",
          {
            params: { id: row["id"] },
          },
          {
            headers: {
              //头部参数
              "Content-Type": "application/json",
            },
          }
        )
        .catch((error) => console.log(error));
      if (response.status === 200) {
        this.$message({
          message: "恭喜你，这是一条成功消息",
          type: "success",
        });
      } else {
        this.$message.error("错了哦，这是一条错误消息");
      }
    },
    handleEdit(index, row) {
      this.$router.push({
        name: `useredit`, // 只是把query改了，其他都没变
        query: {
          id: row["id"],
        },
      });
    },
    async searchUser() {
      const response = await http
        .post(
          "/users/search",
          {
            params: { name: this.search_input },
          },
          {
            headers: {
              //头部参数
              "Content-Type": "application/json",
            },
          }
        )
        .catch((error) => console.log(error));
      if (response.status === 200) {
        this.$message({
          message: "恭喜你，这是一条成功消息",
          type: "success",
        });
      } else {
        this.$message.error("错了哦，这是一条错误消息");
      }
      this.tableData = response.data;
    },
  },
};
</script>

<style scoped>
.search {
  width: 200px;
  margin-bottom: 10px;
  margin-right: 5px;
}
