<template>
    <div>
        <el-card class="filter-container" shadow="never" style="margin-bottom=30px;">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button
          style="float:right"
          type="primary"
          @click="searchPort"
          size="small">
          查询搜索
        </el-button>
        <el-button
          style="float:right;margin-right: 15px"
         
          size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true"  size="small" label-width="140px">
          <el-form-item label="输入搜索：">
            <el-input  class="input-width" placeholder="帐号" clearable v-model="search_input"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card> 
    <el-card class="operate-container" shadow="never" style="margin-bottom=30px;">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
      <el-button size="mini" class="btn-add" @click="dialogFormVisible=!dialogFormVisible" style="margin-left: 20px">添加</el-button>
      <el-button type="primary" icon="el-icon-refresh" circle @click="refresh"></el-button>
    </el-card>
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
            prop="id"
            label="船号"
            align="center"

            width="100">
            </el-table-column>
            <el-table-column
            prop="name"
            label="港口名"
            align="center"

            >
            </el-table-column>
            <el-table-column
            prop="nowCity"
            label="所在城市"
            
            align="center"

            >
            
            </el-table-column>
           
            <el-table-column label="操作" align="center" width="180">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog title="添加港口" :visible.sync="dialogFormVisible" width="30%"  >
            <el-form >
            <el-form-item label="港口名" style="margin-bottom: 10px;" >
                    <el-input  v-model="portForm.port_name" autocomplete="off"></el-input>
                </el-form-item>
                
                <el-form-item label="所在城市 省/市/区" prop="province">
                <el-cascader
                    placeholder="请选择"
                    :options="options"
                    v-model="location"
                    :props="{label: 'name', value: 'name'}"
                    
                ></el-cascader>
                </el-form-item>


               
               
                <el-form-item style="">
                    <el-button type="primary" @click="addPort" style="margin-left: 10vw;margin-top: 10px;" >提交</el-button>
                    <el-button @click="resetForm('shipForm')" >重置</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog title="编辑港口" :visible.sync="dialogEditFormVisible" width="30%"  >
            <el-form >
            <el-form-item label="港口名" style="margin-bottom: 10px;" >
                    <el-input  v-model="portForm.port_name" autocomplete="off"></el-input>
                </el-form-item>
                
                <el-form-item label="所在城市 省/市/区" prop="province">
                <el-cascader
                    placeholder="请选择"
                    :options="options"
                    v-model="location"
                    :props="{label: 'name', value: 'name'}"
                    
                ></el-cascader>
                </el-form-item>

                <el-form-item style="">
                    <el-button type="primary" @click="editPort" style="margin-left: 10vw;margin-top: 10px;" >提交</el-button>
                    <el-button @click="resetForm('shipForm')" >重置</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
import http from "../../shared/Http.js";
import options from '../../assets/address.json'
export default {
    name: "PortManager",
    data() {
      return {
        editId:0,
        dialogFormVisible:false,
        dialogEditFormVisible:false,
        search_input: '',
        options: options,
	    location: [],
        portForm:{
            port_name:'',
            now_city:'',
        },
        tableData: []
      }
    },
    computed:{
        getLocation(){

            return this.location[0]+this.location[1]+this.location[2]
        }
    },
    async beforeMount(){
        const response = await http
            .get("/ports")
            .catch((error) => console.log(error));

        this.tableData = response.data;
    },
    methods: {
        resetForm(){
            Object.assign(this.portForm,{
            port_name:'',
            now_city:'',
        })
        },
      refresh(){
             window.location.reload();
        },
        async addPort(){
            this.portForm.now_city = this.getLocation
            const response = await http
                .post(
                "/ports/add",
                {
                    params: this.portForm,
                },
                {
                    headers: {
                    //头部参数
                    "Content-Type": "application/json",
                    },
                }
                ).catch((error) => console.log(error));
            if (response.status === 200) {
                this.$message({
                message: "恭喜你，这是一条成功消息",
                type: "success",
                });
            } else {
                this.$message.error("错了哦，这是一条错误消息");
            }
            this.dialogFormVisible = false;
            this.resetForm()

        },
        async handleDelete(index, row) {
            const response = await http
                .post(
                "/ports/delete",
                {
                    params: { id: row["id"] },
                },
                {
                    headers: {
                    //头部参数
                    "Content-Type": "application/json",
                    },
                }
                )
                .catch((error) => console.log(error));
            if (response.status === 200) {
                this.$message({
                message: "恭喜你，这是一条成功消息",
                type: "success",
                });
            } else {
                this.$message.error("错了哦，这是一条错误消息");
            }
            },
        async editPort(){
            this.portForm.now_city = this.getLocation

            const response = await http
                .post(
                "/ports/edit",
                {
                    params:   { id: this.editId ,form:this.portForm},
                },
                {
                    headers: {
                    //头部参数
                    "Content-Type": "application/json",
                    },
                }
                ).catch((error) => console.log(error));
            if (response.status === 200) {
                this.$message({
                message: "恭喜你，这是一条成功消息",
                type: "success",
                });
            } else {
                this.$message.error("错了哦，这是一条错误消息");
            }
            this.dialogEditFormVisible = false;
            this.resetForm()
        },
        async handleEdit(index, row){
            
            this.editId = row.id;
            this.dialogEditFormVisible = true;
        },
        async searchPort() {
      const response = await http
        .post(
          "/ports/search",
          {
            params: { name: this.search_input },
          },
          {
            headers: {
              //头部参数
              "Content-Type": "application/json",
            },
          }
        )
        .catch((error) => console.log(error));
      if (response.status === 200) {
        this.$message({
          message: "恭喜你，这是一条成功消息",
          type: "success",
        });
      } else {
        this.$message.error("错了哦，这是一条错误消息");
      }
      this.tableData = response.data;
    },
    }
}
</script>

<style scoped>
.search{
    width: 200px;
    margin-bottom: 10px;
    margin-right: 5px;
}
/* .el-radio{
    font-size: 10px;
} */
.el-form-item{
    margin-bottom: 0;
}
.el-form{
    margin-top: 0;
}
.el-card{
    margin-bottom: 20px;
}
</style>