<template>
  <div>
    <div class="h11">
            <h1><i class="el-icon-s-goods"></i>订单号：{{ id }}</h1></div>
           <div class="main">
            <el-form
                    label-position="right"
                    label-width="80px"
                    :model="formLabelAlign"
                    size="medium"
                    
                >
          <el-form-item label="船只">
              {{ data.ship }}
          </el-form-item>
          <el-form-item label="货物">
            {{ data.cargo }}
           
          </el-form-item>

          <el-form-item label="起始港口">
            {{ data.start_port }}
          </el-form-item>
          <el-form-item label="目的港口">
            {{ data.end_port }}
           
          </el-form-item>
            <el-form-item label="订单备注">
                {{ data.Remark }}
                
            </el-form-item>
            <el-form-item label="订单状态">
                <div v-if="data.status!=='已完成'">
                
                <el-button type="warning">{{ data.status }}</el-button>
            </div>
            <div v-else>
                <el-button type="success">{{ data.status }}</el-button>
            </div>
                
                
            </el-form-item>
            <el-form-item label="订单金额">
                {{ data.price }}
                
            </el-form-item>
            <el-form-item>
                <div v-if="data.status==='待收货'">
                        <h1>正在运输中～ 等待用户收货</h1>
                </div>
                <div v-else-if="data.status==='待发货'">
                                <el-input
                        class="input-width"
                        placeholder="订单号"
                        clearable
                        v-model="diangdanhao"
                        ></el-input>
                        <el-button class="fahuoBtn"
                    style="float: right;display:inline;"
                    type="primary"
                    @click="fahuo"
                    size="small"
         
        >
            确认发货
        </el-button>
                </div>
                
        </el-form-item>
        <el-form-item v-if="data.kuaidiNum!== ''" label="快递单号">
              {{ data.kuaidiNum}}
              
           </el-form-item>
                </el-form>
               
                
           </div>
  </div>
</template>

<script>
import http from "../../shared/Http.js";
function refresh(){
             window.location.reload();
}
export default {
  name: "OrderDesc",
  data() {
    return {
      id: "",
      data: {},
      diangdanhao:"",
    };
  },
  async beforeMount() {
    this.id = this.$route.query.id;
    const response = await http
      .get(
        "/task/desc",
        {
          params: { id: this.id },
        },
        {
          headers: {
            //头部参数
            "Content-Type": "application/json",
          },
        }
      )
      .catch((error) => console.log(error));
    this.data = response.data["orders"][0];
  },
  methods:{
    async fahuo(){
        const response = await http
      .post(
        "/fahuo",
        {
          params: { orderNum:this.id,kuaidiId: this.diangdanhao },
        },
        {
          headers: {
            //头部参数
            "Content-Type": "application/json",
          },
        }
      )
      .catch((error) => console.log(error));
      if (response.status === 200) {
        this.$message({
          message: "恭喜你，这是一条成功消息",
          type: "success",
        });
      } else {
        this.$message.error("错了哦，这是一条错误消息");
      }
      refresh()
    }
  }
};
</script>

