<template>
    <div>
        <el-card class="filter-container" shadow="never" style="margin-bottom=30px;">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button
          style="float:right"
          type="primary"
          @click = "searchShip"
          size="small">
          查询搜索
        </el-button>
        <el-button
          style="float:right;margin-right: 15px"
         
          size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true"  size="small" label-width="140px">
          <el-form-item label="输入搜索：">
            <el-input  class="input-width" placeholder="帐号" clearable v-model="search_input"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card> 
    <el-card class="operate-container" shadow="never" style="margin-bottom=30px;">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
      <el-button size="mini" class="btn-add" @click="dialogFormVisible=!dialogFormVisible" style="margin-left: 20px">添加</el-button>
      <el-button type="primary" icon="el-icon-refresh" circle @click="refresh"></el-button>
    </el-card>
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
            prop="id"
            label="船号"
            align="center"

            width="100">
            </el-table-column>
            <el-table-column
            prop="name"
            label="船名"
            align="center"

            >
            </el-table-column>
            <el-table-column
            prop="type"
            label="船型"
            
            align="center"

            >
            
            </el-table-column>
            <el-table-column
            prop="capacity"
            label="容量（立方英尺）"
           
            align="center"

            >
            
            </el-table-column>
            <el-table-column
            prop="port"
            label="现处港口"
           
            align="center"

            >
            
            </el-table-column>
            <el-table-column
            prop="speed"
            label="速度 km/h"
            width="150"
            align="center"

            > </el-table-column>
            <el-table-column
            prop="price"
            label="每KG/KM价格（元）"
            width="150"
            align="center"

            >
            
            </el-table-column>
            <el-table-column label="操作" align="center" width="180">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog title="添加船只" :visible.sync="dialogFormVisible" width="30%"  >
            <el-form >
            <el-form-item label="船名" >
                <el-input  v-model="shipForm.ship_name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="船型" >
                <el-select v-model="typeValue" placeholder="请选择">
                    <el-option
                        v-for="item in typeOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
                </el-form-item>
                <el-form-item label="容量" >
                    <el-input  v-model="shipForm.capacity" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="现处港口" >
                    <el-select v-model="portValue" placeholder="请选择">
                    <el-option
                        v-for="item in portOptions"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                    </el-option>
                </el-select>
                </el-form-item>
                <el-form-item label="速度 km/h" >
                    <el-input  v-model="shipForm.speed" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="每KG/KM价格（元）" >
                    <el-input  v-model="shipForm.price" autocomplete="off"></el-input>
                </el-form-item>
               
               
                <el-form-item style="">
                    <el-button type="primary" @click="addShip" style="margin-left: 10vw;margin-top: 10px;">提交</el-button>
                    <el-button @click="resetForm('shipForm')" >重置</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
import http from "../../shared/Http.js";

export default {
    name: "ShipInfoManager",
    data() {
      return {
        typeOptions: [{
          value: '1',
          label: '重型'
        }, {
          value: '2',
          label: '中型'
        }, {
          value: '3',
          label: '小型'
    
        }],
        portOptions: [],
        typeValue: '',
        portValue:'',
        dialogFormVisible:false,
        search_input: '',
       
        shipForm:{
            ship_name:'',
            type_id:'',
            capacity:'',
            port_id:'',
            price:'',
            speed:'',
        },
        tableData: []
      }
    },
    async beforeMount(){
        const response = await http
            .get("/ships")
            .catch((error) => console.log(error));
       
        this.tableData = response.data;
        const portResponse = await http
            .get("/ports")
            .catch((error) => console.log(error));
            this.portOptions = portResponse.data;

    },
    methods: {
        resetForm(formName) {
         Object.assign(this.shipForm,{
            ship_name:'',
            type_id:'',
            capacity:'',
            port_id:'',
            price:''
        })
      },
      handleEdit(index, row) {
      this.$router.push({
        name: `shipedit`, // 只是把query改了，其他都没变
        query: {
          id: row["id"],
        },
      });
    },
      refresh(){
             window.location.reload();
        },
        async searchShip() {
      const response = await http
        .post(
          "/ships/search",
          {
            params: { name: this.search_input },
          },
          {
            headers: {
              //头部参数
              "Content-Type": "application/json",
            },
          }
        )
        .catch((error) => console.log(error));
      if (response.status === 200) {
        this.$message({
          message: "恭喜你，这是一条成功消息",
          type: "success",
        });
      } else {
        this.$message.error("错了哦，这是一条错误消息");
      }
      this.tableData = response.data;
    },
    async addShip(){
            this.shipForm.type_id =  this.typeValue
            this.shipForm.port_id =  this.portValue
            console.log(this.shipForm)
            const response = await http
                .post(
                "/ships/add",
                {
                    params: this.shipForm,
                },
                {
                    headers: {
                    //头部参数
                    "Content-Type": "application/json",
                    },
                }
                ).catch((error) => console.log(error));
            if (response.status === 200) {
                this.$message({
                message: "恭喜你，这是一条成功消息",
                type: "success",
                });
            } else {
                this.$message.error("错了哦，这是一条错误消息");
            }
            this.dialogFormVisible = false;
            this.resetForm()

        },
    }
}
</script>

